.header{
  display:flex;
  justify-content: space-between;
  border: 1px solid black;
   
}
.Logo{
    width:200px;
}
.nav-items{   
    padding: 0 10px 0;
    width: auto;
}
.nav-items > ul{
    font-size:24px;
    display:flex;
    list-style-type: none;
}
.nav-items > ul > li{
    padding:10px;
    margin:10px;
}
.res-card{
    margin:5px;
    padding:5px;
    width:200px;
    height:420px;
   
}
.res-container{
    display:flex;
    flex-wrap:wrap;
}
.search{
    padding:30px;
}
