} */
.res-logo {
    width: 200px; /* Specify your desired width */
    height: 170px; /* Specify your desired height */
}
.res-card:hover{
    cursor:pointer;
    border: 1px solid black;
}
.filter-btn{
    margin: 10px;
}

    
